#selected{
    padding: 10px 0 10px 0;
    display: grid;
    grid-template-columns: auto auto 4%;
}
#selected > div{
    display: flex;
    align-items: center;
}
#selected .selected-right{
    justify-content: flex-end;
}
#selected > button{
    padding: 0;
}
.select-color-box > button{
    padding: 2px;
}
#search-select{
    display: flex;
    align-items: center;
    justify-content: center;
}
#search-select > div{
    width:100px;
    margin: 5px;
}

#search-tab{
    padding: 3px 20px 5px 20px;
}
.search-tab-title{
    margin-bottom: 10px;
}
#exchange-tab .search-exchange{
    width: 80%;
}
.chart-control-btns{
    display: grid;
    grid-template-columns: 12% 76% 12%;
}
.chart-control-btns .integrate{
    display: flex;
    align-items: center;
    justify-content: center;
}
.select-period-btns > div{
    padding: 2px;
}
.select-period-btns > div > div{
    width: 100%;
    background:#ffcec9;
    color:#f83c23;
    font-size: medium;
    box-shadow: 1px 1px 2px 1px rgb(153, 63, 63);
}
.select-period-btns > div > div:hover,
.select-period-btns > div > div:focus{
    background:rgb(255, 174, 174)
}